/* eslint-disable no-lone-blocks */
import { useState } from 'react';
import photo from"./images/mine.jfif";
import './App.css';
import { FaFacebookF, FaLinkedinIn, FaHeart } from "react-icons/fa";
import Display from "./Display";


function App() {

    const [skills , setSkills] = useState("My name is Titi")

    const handleName = () => {
      setSkills("My name is Titi")
    }

    const handleSkills = () => {
      setSkills("I am a Frontend Developer")
    }
  
    const [count, setCount] = useState(0)

    return(
      <>
          <div className="app">
            <img src={photo} alt="Titi" className="photo"/> 
            <Display tech={skills}/> 
          
            <div className="btns">
              <button onClick={handleName}>Name</button>
              <button onClick={handleSkills}>Skills</button>
            </div>
            <div className="likes">
            {count}
              <FaHeart onClick={() => setCount(count + 1)} />
              </div> 
            <a href="https://www.facebook.com">
              <i><FaFacebookF /></i>
            </a>
            <a href="https://www.linkedin.com">
              <i><FaLinkedinIn /></i>
            </a>
          </div>
      </>
    );
}

export default App;

